@font-face {
	font-family: 'Unrulyness';
	src: local('Unrulyness'), url(../../fonts//Unrulyness.ttf) format('truetype')
}

.SubHeader {
	font-size: 50px;
	color: #ff5b5b;
	font-family: "Unrulyness", sans-serif;
	padding: 0;
	margin: 0;
	transition: all 0.3s;
}

@media (min-width: 580px) {
	.SubHeader {
		font-size: 60px;
	}
}

@media (min-width: 740px) {
	.SubHeader {
		font-size: 75px;
	}
}
